
  @media screen and (max-width: 100%){
    .container-slider {
      margin: 100px 10px 0;
    }
  }
  .slide {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    transition: opacity ease-in-out 0.3s;
    z-index: -1;
  }
  .slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
   
  }
  .active-anim {
    opacity: 1;
  }
  
  .btn-slide {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: transparent;
    border: 1px solid transparent;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

  }
  
  .btn-slide img {
    width: 25px;
    height: 25px;
    pointer-events: none;

  }
  .prev {
    top: 50%;
    left: 20px;
    transform: translateY(-60%);

  }
  .next {
    top: 50%;
    right: 20px;
    transform: translateY(-60%);
   
  }
  
  
  .container-dots {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
  }
  .dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border:2px solid #cfecf7;
    margin: 0 5px;
    background: #cfecf7;
  }
  .dot.active {
    background: #1c96c5;
  }